import requireurl from '../../requist/requist.js'
export default {
	data() {
		return {
			secckey:'',
			timer:null,
			num:0
		}
	},
	methods: {
		//使用手机号码登录
		usermobile(){
			uni.navigateTo({
			    url: '../login/login'
			});
		},
		//用户取消登录
		backindex(){
			uni.reLaunch({
			    url: '../index/index'
			});
		},
		//获取微信session_key
		getscodesuccess(e){
			console.log('发送code成功')
			console.log('code' + e.data.key)
			this.secckey = e.data.key
			
		},
		
		
		//微信登录
		wxlogins(){
			var _that = this
			wx.login({
			  success (res) {
			    if (res.code) {
			      //发起网络请求
				  requireurl.getData(`/users/wx/session_key/?code=${res.code}`,_that.getscodesuccess,_that.file)
			    } else {
			      console.log('登录失败！' + res.errMsg)
			    }
			  }
			})
		},
		
		
		//获取手机号
		getPhoneNumber(e){
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			console.log(e)
			let obj = {
				key:this.secckey,
				encryptedData:e.detail.encryptedData,
				iv:e.detail.iv
			}
			console.log(obj)
			console.log('执行了')
			if(this.secckey === ''){
				uni.hideToast();
				uni.showToast({
				    title: '状态已失效请重试',
					icon :'none',
				    duration: 1500
				});
				this.wxlogins()
			}
			requireurl.request(`/users/wx/login/`,obj,this.wxloginca,this.file)
		},
		//登录失败
		file(err){
			uni.hideToast();
		},
		wxloginca(e){
			console.log('登录了')
			console.log(e)
			uni.hideToast();
			if(e.data.errno === 0){
				uni.setStorage({
				    key: 'storage_mobile',
				    data: e.data.mobile,
				    success: function () {
				        console.log('success')
						uni.navigateTo({
							url:'../sendcode/sendcode'
						})
				    }
				});
			}else if(e.data.errno === 1){
				uni.setStorageSync('storage_user', e.data.user_data);
				uni.reLaunch({
				    url: '../index/index'
				});
			}else if(e.data.errno === -1){
				uni.showToast({
				    title: e.data.err,
					icon :'none',
				    duration: 1500
				});
			}
		}
	},
	
	
	mounted() {
		this.wxlogins()
	
	}
}
